<template>
    <Header :homeDisplay="this.homeDisplay"/>
    <h3> [사용자 정보] </h3>
    <div class="container">
        <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="this.user.username">
                    사용자 : {{ this.user.username }}
        </div>
        <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="this.user.email">
                    메  일 : {{ this.user.email }}
        </div>
        <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="this.user.telnnum">
                    전화번호 : {{ this.user.telnnum }}
        </div>
        <img :src='this.imgUrl' v-if="this.imgUrl" />    
    </div>

</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";
const homeDisplay = ref(true);
const imgUrl= ref('');

export default{
    name: "UserInfo",
    components: {
        Header,
    },
    data() {
        return{
            user:'',
            homeDisplay:homeDisplay,
            imgUrl:imgUrl
        }
    },
    mounted() {
        this.user = this.$store.state.auth.user;
        if( this.user.photoUrl != null) {
            this.imgUrl = this.user.photoUrl;
        }        
    }
}

</script>

